body {
  overflow-x: hidden;
  background-color: #f1f1f1;
  position: relative; }

a {
  color: #000; }

a:hover {
  color: #000; }

a:focus {
  color: #000; }

::-webkit-scrollbar {
  width: 8px; }

::-webkit-scrollbar-track {
  background-color: #f1f1f1; }

::-webkit-scrollbar-thumb {
  background: #6441A4;
  border-radius: 5px; }

*:focus {
  box-shadow: none;
  outline: none !important; }

input[type="file"] {
  display: none; }

select {
  width: 100%;
  background-color: #e6e6e6;
  border: 2px solid #fff;
  color: #6441A4;
  transition: all 0.15s ease; }

input[type] {
  background-color: #e6e6e6;
  border: 2px solid #fff;
  color: #000;
  transition: all 0.15s ease; }

textarea:hover {
  border: 2px solid #ccc;
  box-shadow: none; }

textarea:focus {
  background-color: #e6e6e6;
  border: 2px solid #7a5fb9;
  box-shadow: none;
  color: #000; }

input[type]:hover {
  border: 2px solid #ccc; }

input[type]:focus {
  background-color: #e6e6e6;
  border: 2px solid #6441A4;
  box-shadow: none;
  color: #000; }

.swal2-popup .swal2-styled:focus {
  box-shadow: none; }

